import LineGradient from "../components/LineGradient";
import { motion } from "framer-motion";
import project1 from "../assets/project-1.jpg";

const container = {
    hidden: {},
    visible: {
        transition: { staggerChildren: 0.2 }
    }
};

const projectVariant = {
    hidden: { opacity: 0, scale: 0.8 },
    visible: { opacity: 1, scale: 1}
}

const Project = ({ title, src, description, href }) => {
    const overlayStyles = `absolute h-full w-full opacity-0 hover:opacity-90 transition duration-500 bg-grey z-30 
        flex flex-col justify-center items-center text-center p-16 text-deep-blue`;
    const projectTitle = title.split(" ").join("-").toLowerCase();
    return (
        <motion.div variants={projectVariant} className="relative">
            <div className={overlayStyles}>
                <a href={href}
                   target="_blank"
                   rel="noreferrer">
                <p className="text-2xl font-playfair">{title}</p>

                <p className="mt-7">
                    {description}
                </p>
                </a>
            </div>
            <img alt={projectTitle} img src={src} />
        </motion.div>
    )
}



const Projects = () => {
    return (
        <section id="projects" className="pt-48 pb-48">
            {/*heading */}
            <motion.div
                     className="md:w-2/4 mx-auto text-center"
                     initial="hidden"
                     whileInView="visible"
                     viewport={{ once: true, amount: 0.5 }}
                     transition={{ duration: 0.5 }}
                     variants={{
                          hidden: { opacity: 0, x: -50 },
                          visible: { opacity: 1, x: 0}
                     }}
                >
                    <div>
                    <p className="font-playfair font-semibold text-4xl">
                        <span className="text-blue">PRO</span>JECTS
                        </p>
                        <div className="flex justify-center mt-5">
                        <LineGradient width="w-1/3" />
                        </div>
                    </div>
                     
                     <p className="mt-10 mb-7">

                        
                     </p>


                </motion.div>   
                {/* projects */}
                <div className="flex justify-center">
                <motion.div
                     className="sm:grid sm:grid-cols-3"
                     initial="hidden"
                     whileInView="visible"
                     viewport={{ once: true, amount: 0.5 }}
                     variants={container}
                >
                    {/* Row 1 
                    <div className="flex justify-center text-center items-center p-10 bg-red max-w-[400px] max-h-[400px] text-2xl font-playfair font-semibold">
                      user interfaces
                    </div>*/}
                    <Project title="Project 1" src={project1} description="A Random Quote Generator ReactJs App" href="https://daemusin12.github.io/randomquote/"/>
                    {/*<Project title="Project 2" />

                     row 2
                     <Project title="Project 3" />
                     <Project title="Project 4" />
                     <div className="flex justify-center text-center items-center p-10 bg-blue max-w-[400px] max-h-[400px] text-2xl font-playfair font-semibold">
                      smothanga
                    </div>*/}
                    </motion.div>
                </div>
        </section>
    )
}

export default Projects;